on.href = \"#/index/tabs1\"\n\t\t\t\t},\n\t\t\t\t//评论成功与否的方法\n\t\t\t\tnogu() {\n\n\t\t\t\t\tvar self = this;\n\t\t\t\t\tvar cookie = self.getCookie(\"accesstoken\");\n\t\t\t\t\tconsole.log(\"https://cnodejs.org/api/v1/topic/\" + self.$route.params.id + \"/replies\")\n\t\t\t\t\t//判断cookie中是否有accesstoken\n\t\t\t\t\tif(cookie) {\n\t\t\t\t\t\t$.ajax({\n\t\t\t\t\t\t\t\turl: \"https://cnodejs.org/api/v1/topic/\" + self.$route.params.id + \"/replies\",\n\t\t\t\t\t\t\t\ttype: \"POST\",\n\t\t\t\t\t\t\t\tdata: {\n\t\t\t\t\t\t\t\t\taccesstoken: cookie,\n\t\t\t\t\t\t\t\t\tcontent: self.textValue\n\t\t\t\t\t\t\t\t},\n\t\t\t\t\t\t\t\t//请求成功的回调\n\t\t\t\t\t\t\t\tsuccess: function(data) {\n\t\t\t\t\t\t\t\t\tconsole.log(data);\n\t\t\t\t\t\t\t\t\talert(\"评论成功\");\n\t\t\t\t\t\t\t\t\tself.bool = true;\n\n\t\t\t\t\t\t\t\t\t//用定时器定时让页面隐藏\n\t\t\t\t\t\t\t\t\tsetTimeout(function() {\n\t\t\t\t\t\t\t\t\t\tself.bool = false;\n\t\t\t\t\t\t\t\t\t}, 500);\n\n\t\t\t\t\t\t\t\t\t//清空评论框输入的内容\n\t\t\t\t\t\t\t\t\tself.textValue = \"\";\n\t\t\t\t\t\t\t\t\t//重新刷新页面的数据让评论显示出来\n\t\t\t\t\t\t\t\t\tself.getDetail();\n\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t})\n\t\t\t\t\t\t\t//重新判断是否有用户存在才可以评论\n\t\t\t\t\t} else {\n\t\t\t\t\t\talert(\"请登录后再评论\");\n\t\t\t\t\t\tself.bool = true;\n\n\t\t\t\t\t\tsetTimeout(function() {\n\t\t\t\t\t\t\tself.bool = false;\n\t\t\t\t\t\t}, 500);\n\t\t\t\t\t}\n\t\t\t\t}\n\t\t\t},\n\t\t\tmounted() {\n\t\t\t\t//获取Url数据传过来的ID\n\t\t\t\tconsole.log(this.$route.params.id);\n\t\t\t\t//可以让他先执行一次\n\t\t\t\tthis.getDetail();\n\n\t\t\t},\n\t\t\t//注册方法\n\t\t\tcomponents: {\n\t\t\t\tRefresh\n\t\t\t}\n\t}\n</script>\n\n<style>\n\timg {\n\t\twidth: 100%;\n\t\theight: 250px;\n\t}\n\t\n\t.gtnlo {\n\t\tz-index: 10;\n\t\twidth: 100px;\n\t\theight: 100px;\n\t\tposition: fixed;\n\t\tright: 10px;\n\t\tbottom: 30px;\n\t\tcursor: hand;\n\t}\n\t\n\t.gtnlo img {\n\t\twidth: 100px;\n\t\theight: 100px;\n\t}\n\t\n\t.ggen {\n\t\theight: 100%;\n\t\tmargin-top: 56px;\n\t}\n\t\n\t.ggen-2 {\n\t\twidth: 100%;\n\t\theight: 70px;\n\